h1{
    margin-top: 2rem;
    margin-bottom: 1rem;
    margin-left: 2.5rem;
}
.GearUp_layout{
    box-sizing: border-box;
    display: flex;
    flex-wrap: nowwrap;
    padding: 1rem;
    align-items: flex-start ;
    overflow-x: scroll;
    scrollbar-color: red orange;
    scrollbar-width: thin;
    gap: 2rem;
    width:100%;
    font-weight: 500;
    padding: 0 2rem;
}
.GearUp_layout img{
    height: 55vh;
    width: 49vh;
    margin-top: 2px;

}
.GearUp_layout .name{
     font-size: 1.4rem;
}
.GearUp_layout .price{
    font-size: 1rem;
}
.item_Card{
    margin-bottom: 4rem;
    padding: 1rem 0.3rem;
    transition: all 0.6s;
}
.item_Card:hover{
    box-shadow: 0rem 1rem 1rem rgb(144, 141, 141);
}
::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888; 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }
  .item_card_button{
    display: flex;
    justify-content:space-between ;
    padding-right:3rem ;
  }
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  background-color: #ffffff;
  padding: 20px;
  padding-top: 2.3rem;
  border-radius: 4px;
  max-width: 60vh;
  text-align: center;
  position: relative;
}

.close {
  
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 24px;
  cursor: pointer;
}
.modal_button{
  margin-top: 1.8rem;
  display: flex;
  justify-content: space-evenly;
}
.modal_button .button{
  background-color: rgb(95, 140, 255);
  padding: 1rem 2rem;
  border: 2px rgb(255, 255, 255) solid;
  border-radius: 2rem;
  font-size: 1rem;
}
.modal_button .button:hover{
  background-color: rgb(50, 194, 139);
  color: #f1f1f1;
}
.modal_price{
  font-size:1.5rem ;
}