h1{
    margin-left: 5%;
}
.main_box{
    display: flex;
    justify-content: space-evenly;
    height: auto;
    /* width: 100%; */
    /* justify-content: space-evenly; */
}
.box{
    width: 100%;
    display: flex;
}

.floatImage {
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.4);
    margin: 15px;
    margin-right: 0px;
    z-index: 2;
    height: auto;
  }
  
  figure {
    width: 25%;
    float: right;
  }
  
  figcaption {
    background-color: rgba(255, 255, 255, 0.6);
    height: 1.2em;
    width: 100%;
    margin-top: -6rem;
    margin-left: 2rem;
    text-align: start;
    color: rgb(0, 0, 0);
    z-index: 11;
    font-family: 'Open Sans', sans;
  }
  figcaption button{
    border: none;
    background-color: rgb(255, 255, 255) ;
    border-radius: 1rem ;
    font-size: 1.2rem;
    width: auto;
    padding: .5rem;
    transition: all 0.4s ;
  }
  figcaption button:hover{
    background-color: black;
    color: aliceblue;
  }