.header-slider {
    position: relative;
  }
  
  .slider-image {
    width: 100%;
    height: 70vh;
    overflow: hidden;
  }
  
  .slider-navigation {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    justify-content: space-between;
    width: 100%;
    
  }
  
  .slider-navigation button {
    margin: 0 5px;
    padding: 5px 10px;
    background-color: #555555;

    color: #fff;
    border: none;
    cursor: pointer;
  }
