.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #f2f2f2;
    padding: 10px;
    height: 8vh;
  }
  
  .logo a {
    font-size: 1.5rem;
    text-decoration: none;
    color: #333;
    letter-spacing: .4rem;
    /* font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif; */
  }
  
  .nav-links {
    list-style: none;
    display: flex;
    align-items: center;
    color: black;
  }
  
  .nav-links li {
    margin-right: 15px;
    padding: 2rem;
  }
  
  .nav-links a {
    text-decoration: none;
    color: #555;
    transition: color 0.3s ease;
    font-size: 1.3rem;
  }
  
  .nav-links a:hover {
    color: #000000;
  }
  .nav-links li :hover{
    border-bottom: 1px solid rgb(84, 84, 84);
    
  }
  